<template>
    <div class="resetBtnRoot">
        <button class="resetBtn" @click="togglePrompt">
            reset data
        </button>
        <!-- <br />
        <span class="menuBtnTitle">Button to activate menu:</span>
        <br />
        <button class="menuBtn" :class="midClass" @click="setMid">
            mid mouse btn
        </button>
        <button class="menuBtn" :class="gClass" @click="setG">
            'G' btn
        </button> -->
        <div v-if="promptComputed" id="prompt">
            <p>Are you sure you want to reset your mindmap?</p>
            <button @click="resetLocalStorage">Yes, clear data</button>
            <button @click="togglePrompt">No</button>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    data(){
        return{
            prompt: false,
            midBtn: true,
            gBtn: false
        }
    },
    mounted(){
        if(this.getMenuBtn == "mouse") this.setMid();
        if(this.getMenuBtn == "g") this.setG();
    },
    computed: {
        ...mapGetters(['getMenuBtn']),
        promptComputed(){
            return this.prompt;
        },
        midClass(){
            if(this.midBtn) return "active"
            return "";
        },
        gClass(){
            if(this.gBtn) return "active"
            return "";
        }
    },
    methods: {
        ...mapActions(['setMenuBtn']),
        resetLocalStorage(){
            window.localStorage.clear();
            this.prompt = false;
        },
        togglePrompt(){
            this.prompt = !this.prompt;
            console.log("prompt", this.prompt)
        },
        setMid(){
            this.setMenuBtn('mouse');
            this.midBtn = true;
            this.gBtn = false;

        },
        setG(){
            this.setMenuBtn('g');
            this.midBtn = false;
            this.gBtn = true;
        }
    }
}
</script>

<style lang="scss">
.resetBtnRoot{
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0.2;
    transition: opacity 0.15s ease-out;

    &:hover{
        opacity: 1;
    }


    .resetBtn, .menuBtn, .menuBtnTitle{
        margin: 10px;
    }
    .menuBtn{
        color: gray;
    }
    .menuBtn:hover{
        color: black;
        cursor: pointer;
    }
    .active{
        background-color: #75e06b;
        border: 2px solid #539e4d;
        color: black;
    }

    #prompt{
        width: 200px;
        padding: 20px;
        border-radius: 5px;
        background-color: white;
        border: 1px solid #eaeaea;
        position: absolute;
        top: -55vh;
        left: -50vw;
    }
}
</style>